import student from './menu/student'
import lesson from './menu/lesson'
import iae from './menu/iae'
import hours from './menu/hours'
import has from './menu/has'
import fee from './menu/fee'
import system from './menu/system'

let menu = [] 
menu.push(student)
menu.push(lesson)
menu.push(hours)
menu.push(has)
menu.push(fee)
menu.push(iae)
menu.push(system)
export default menu


